export default {"name":"龙泉茶","img":"\\wulongcha\\longquancha\\img_201911221043068e0ctj.jpg","desc":["龙泉茶又称龙潭包种茶，轻度半发酵乌龙茶，是台湾龙潭乡的特产，“龙泉飘香”就是她的金字招牌。民国七十二年(公元1983年)当时的省主席李登辉先生将龙潭包种茶命名为“龙泉茶”。龙泉茶外观青绿色，条索紧凑，无梗，干茶具有浓烈的兰花清香。冲泡后，茶香芬芳扑鼻，汤色黄绿清澈。茶汤滋味有过喉圆滑甘润之感，回甘力强。具有“香、浓、醇、韵、美”五大特色。为台湾十大名茶之一。"],"menus":[{"name":"龙泉茶茶叶历史","content":[{"text":"龙潭乡是台湾最早推广茶叶栽培的地方，所产的龙潭包种茶是全台之冠，也是日治时期台湾主要产茶区，虽然光复后茶市场一度衰竭，但在政府大力推动复兴政策后，龙潭茶恢复活络的状态。民国七十二年(公元1983年)当时的省主席李登辉先生将龙潭包种茶命名为“龙泉茶”。 台湾龙潭乡地势高亢平坦，海拔在300~400公尺之间，龙潭乡境内多平缓丘陵地，全年气候温暖多雨，每日清晨和黄昏都有一层薄雾笼罩，茶叶生长经此湿气滋润，所制成的茶叶品质醇厚，香气怡人，大部份土质为强酸性的台地砖红土，最适合栽种茶树，逐渐发展成台湾主要产茶区之一。"},{"text":"包种茶曾于1982年荣获全省机采优良包种茶比赛冠军，民国1983年更获李登辉总统先生(当时任省主席)命名龙潭乡所产的茶叶为“龙泉茶”。由于当地乡公所、农会配合有关单位，大力推广改进茶叶产制品质，并将乡内四处茶园规划成为本省最具特色及规模的观光茶园，观光茶园主要位于三林及高原村，以铜锣圈观光茶园及三林观光茶园分布最广。有机会到龙潭观光茶园，除了享受原始的大自然茶园风光外，再来一杯“龙泉茶”，轻轻地啜饮一口，她的芬芳，她的甘醇，那种怀古幽情的享受，将使您立即忘却人间的烦恼。\"龙泉茶\"由于受地理位置及气候的影响,茶的产量因为管理技术的更新而不断提高。"}]},{"name":"龙泉茶茶叶特点","content":[{"text":"龙泉茶干茶青绿，条索紧凑，无梗，透兰花香，此茶干茶香气甚浓。冲泡后，茶香芬芳扑鼻，汤色黄绿清澈。茶汤滋味有过喉圆滑甘润之感，回甘力强。具有“香、浓、醇、韵、美”五大特色。为台湾十大名茶之一。"},{"src":"\\wulongcha\\longquancha\\img_20191122104306apyl6c.jpg"}]},{"name":"龙泉茶加工工艺","content":[{"src":"\\wulongcha\\longquancha\\img_20191122104306k2fp2v.jpg"},{"text":"龙泉茶采制分为春夏秋冬四季，3月中旬—5月上旬为春茶;5月下旬-8月中旬为夏茶,8月中旬—10月下旬为秋茶;10月下旬—11月中旬为冬茶,其中以春茶、秋茶及早期冬茶品质较佳。鲜叶采摘标准为与新梢顶芽开面采二、三叶。不同品种、不同时间采的鲜叶，应分开制作。"},{"text":"龙泉茶为轻度半发酵\n        乌龙茶\n        ，龙泉茶的初制工艺依次为分日光(或室内加温)萎凋、室内萎凋(静置与搅拌)、炒青、揉捻、初干、焙干等工序。"}]},{"name":"龙泉茶储藏方法","content":[{"text":"1、罐贮法"},{"text":"罐藏法比较简单，只要将从市场上买回的茶叶放入洁净的罐内即可。不过，为了更好地保持罐内干燥，可以在罐内放入1-2小包硅胶或保鲜剂，这样效果更好。"},{"text":"2、袋藏法"},{"text":"目前用得最多的是用塑料袋保藏茶叶，这也是家庭贮藏茶叶最简便、最经济的方法之一。用塑料袋包装茶叶能否起到有效的保藏作用，关键是一要茶叶本身干燥，二要选择好包装材料。"},{"text":"好的塑料包装材料必须做到以下几点：首先必须是食品包装袋，否则会对身体健康不利;其次。塑料袋材料的密度要高，尽量减少气体的通透，因此，宜选用低压塑料材料做成的包装袋，另外，塑料袋本身不应有漏洞和异味，强度要高一些。"},{"text":"3、冷藏法"},{"text":"用冰箱冷藏茶叶，可以收到令人满意的效果。但有两点必须注意：一是要防止冰箱中的其他食品的气味(如鱼腥味)污染茶叶;二是茶叶本身必须是干燥的。"}]},{"name":"龙泉茶冲泡方法","content":[{"text":"(1)看干茶：先审察干茶的形状与色泽，以了解茶叶品质特性。"},{"text":"(2)烫\n        茶具\n        ：以沸水冲泡洗茶碗、茶具以符卫生。"},{"src":"\\wulongcha\\longquancha\\img_20191122104306hkxcuk.jpg"},{"text":"(3)放置茶叶：参酌茶叶特性，放置茶壶容量1/3的茶叶。"},{"text":"(4)冲泡：以沸水冲入壶中，冲满即刻盖好，冲泡开水温度以95-100℃为宜，可以多次冲泡以供长时间品饮。"},{"text":"(5)冲茶碗：将热水冲入杯内，用杯盖轻拨泡沫，将杯盖上泡沫冲掉盖上杯盖。"},{"text":"(6)冲泡时间：冲泡时间由短而长，第一次短而后逐次增长。泡茶之时间长短不同，茶汤中可溶物的量与质亦是不同，因此冲泡茶的时间长短直接影响茶汤品质。"},{"text":"(7)温杯：将茶杯先用沸水冲净烫温，以助长香味兼重卫生。"},{"text":"(8)倾倒茶汤：将冲泡出来之茶汤，先倾入茶海，使其浓度匀一，茶末沉淀，然后均匀注入茶杯供饮。"},{"text":"(9)品茶：龙泉茶茶香芬芳扑鼻，汤色黄绿清澈。茶汤滋味有过喉圆滑甘润之感，回甘力强。有生津解渴，提神醒脑之效。"}]},{"name":"龙泉茶功效作用","content":[{"text":"1、饮用龙泉茶有瘦身的作用"},{"text":"能够刺激胰脏脂肪分解酵素的活性，减少糖类和脂肪类食物被吸收，消食去腻、生津利尿、减肥健美。"},{"src":"\\wulongcha\\longquancha\\img_20191122104306ct0cm6.jpg"},{"text":"2、预防老化"},{"text":"龙泉茶中还含有大量的茶多酚，可以提高脂肪分解酶的作用，降低血液中的胆固醇含量，有降低血压、抗氧化、防衰老等作用。"},{"text":"3、阻止发生齿垢，预防蛀牙"},{"text":"龙泉茶中含有的多酚类具能够抑制齿垢酵素产生的功效，吃饭之后饮用一杯龙泉茶，可以防止齿垢和蛀牙的发生。"},{"text":"4、消除危害美容与健康的活性氧"},{"text":"减慢几乎老化或产生皱纹。每天喝1公升龙泉茶还能改善皮肤过敏。"},{"text":"5、龙泉\n        茶具\n        有提神益思，解除疲倦的作用"},{"text":"龙泉茶叶中有咖啡碱，咖啡碱具有兴奋中枢神经、增进思维、提高效率的功能。"},{"text":"6、解热防署、杀菌消炎、解毒防病。"},{"text":"龙泉茶除了与一般茶叶具有提神益思，消除疲劳、生津利尿、解热防署、杀菌消炎、解毒防病、消食去腻、减肥健美等保健功能外,还突出表现在降血脂、抗衰老等特殊功效。"}]},{"name":"龙泉茶历史故事","content":[{"text":"关于龙泉茶还有一个美丽的传说：据说在很早以前，在深山中有一个深千尺的幽潭。那里薄雾笼罩，人际罕至，由于吸收了天地之灵气，那里的水就有了一种神奇的功效。一次有一条小龙负伤，准备回去疗伤。当他路过深潭时就感觉到潭里所散出的灵气，于是就决定在这里面疗伤便纵身跃入潭底。不知过了多久，他的伤好，在他养伤的这段期间吸收了潭水的灵气使他的身体发生了很大的变化，于是索性就在那里修身养性。"},{"text":"龙泉茶一年，山下的村庄发生旱灾，村民无奈就只好到深山寻找水源。小龙知道了村民的困境就化身一棵茶树，利用茶树的芳香和潭的灵气引领村民找到了水。当村民找到水源时已经又饿又渴，顾不了那么多舀起水就喝，看到潭边长着一株有异香的茶树，就摘几片放在嘴里嚼了起来，顿时感觉身上的疲劳减了一大半。于是村民就把水引到山下，也把那棵茶树移植到家里。第二年春天，茶树经过细心照料长得更加茂盛，有个制茶能人便把它摘下来制成茶。他制成后就有一股幽香飘出，村民闻香赶来，他就把茶拿出来和村人一起品尝，当大家看到茶入杯时就惊呆：茶所冒出的热气犹如一条龙在杯底绕一圈，然后缓缓上升就向东飘散了，犹如蛟龙在空中起舞然后突然间隐身不见了。于是大家就把引水的潭叫做龙潭，把这茶称做“龙泉茶”。由于这茶有异香，因此很快就被其他地方的村民知道，据说方圆千里没有人不知道的。"}]}]}